@charset "UTF-8";
/* GLOBAL */ /* здесь используйте глобальные переменные */
@font-face {
  font-family: "Montserrat";
  src: url("/fonts/Montserrat-VariableFont_wght.ttf") format("woff2 supports variations"), url("/fonts/Montserrat-VariableFont_wght.ttf") format("woff2-variations");
  font-weight: 100 900;
  font-display: swap;
}
/* здесь используйте глобальные селекторы img, body, html и так далее*/
*,
*::after,
*::before {
  box-sizing: border-box;
}

.page {
  height: 100%;
}

main {
  flex: 1 0 auto;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
    max-width: 768px;
  }
}
@media (min-width: 1440px) {
  .container {
    padding: 0 40px;
    max-width: 1440px;
  }
}

body {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #333333;
  background-color: #ffffff;
  margin: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.all-button {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #ffffff;
  background-color: #7859cf;
  text-align: center;
  text-decoration: none;
  display: block;
  padding: 8px 0;
  width: 100%;
  border-radius: 4px;
  border: none;
}

.all-button:hover,
.all-button:focus {
  background-color: #9070ec;
}

.all-button:active {
  background-color: #593cac;
}

.all-button:disabled {
  background-color: #bdbdbd;
}

/* HELPERS */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

/* BLOCKS */
.header {
  background-color: #ffffff;
  color: #111111;
  position: relative;
}

.header-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.header-logo {
  margin-right: auto;
  text-decoration: none;
}
@media (min-width: 768px) {
  .header-logo {
    margin-right: 0;
    order: 1;
    display: flex;
    align-items: center;
  }
}

@media (min-width: 1440px) {
  .header-logo__image {
    margin-right: 20px;
  }
}

.header-logo-text {
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: #333333;
  display: none;
}
@media (min-width: 1440px) {
  .header-logo-text {
    display: block;
    width: 130px;
  }
}

/*стилизация user-action*/
.user-actions {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: row;
}
@media (min-width: 768px) {
  .user-actions {
    order: 3;
  }
}

.user-action {
  padding: 22px;
}
@media (min-width: 768px) {
  .user-action {
    padding: 32px 20px;
  }
}

.user-action-link {
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  text-decoration: none;
  text-transform: uppercase;
  color: #333333;
}
@media (min-width: 768px) {
  .user-action-link {
    display: flex;
  }
}

.user-action:hover .user-action-link {
  color: #7859cf;
}

.user-action svg {
  fill: #333333;
}

.user-action:hover svg {
  fill: #7859cf;
}

.user-action:active {
  background: #f0ebff;
}

.user-action:active .user-action-link {
  color: #7859cf;
}

@media (min-width: 1440px) {
  .user-action-link svg {
    margin-right: 8px;
  }
}

.user-action-login,
.user-action-cart {
  display: none;
}
@media (min-width: 1440px) {
  .user-action-login,
.user-action-cart {
    display: block;
  }
}

/*стилизация меню навигацмм*/
.main-nav-button {
  border: none;
  cursor: pointer;
  background-color: #ffffff;
  margin: 0;
  padding: 0;
  margin-left: 22px;
}
@media (min-width: 768px) {
  .main-nav-button {
    display: none;
  }
}

.main-nav-button:active {
  background-color: #f0ebff;
}

.main-nav-button .main-nav-close {
  display: none;
}

.main-nav-button--active .main-nav-burger {
  display: none;
}

.main-nav-button--active .main-nav-close {
  display: block;
}

@media (min-width: 768px) {
  .main-nav {
    order: 2;
  }
}

.main-nav__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: none;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
  }
}

.main-nav__list--active {
  display: block;
  position: absolute;
  top: 100%;
  right: 0;
  z-index: 10;
  background-color: #ffffff;
  width: 100%;
  text-align: center;
  border-top: 1px solid #f0ebff;
}

.main-nav__list--active .main-nav__item {
  padding: 20px 0;
  border-bottom: 1px solid #f0ebff;
}

.main-nav__list--active .main-nav__item:active {
  background-color: #f0ebff;
}

@media (min-width: 768px) {
  .main-nav__item {
    padding: 32px 20px;
  }
}

@media (min-width: 768px) {
  .main-nav__item--current {
    border-bottom: 2px solid #7859cf;
  }
  .main-nav__item--current .main-nav__link {
    color: #7859cf;
  }
}

@media (min-width: 768px) {
  .main-nav__item:hover .main-nav__link,
.main-nav__item:focus .main-nav__link {
    color: #7859cf;
  }
}

@media (min-width: 768px) {
  .main-nav__item:active {
    background-color: #f0ebff;
  }
  .main-nav__item:active .main-nav__link {
    color: #7859cf;
  }
}

.main-nav__link {
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: #333333;
  text-decoration: none;
  text-transform: uppercase;
}

