.range-scale {
  position: relative;
  height: 2px;
  margin-bottom: 25px;
  background-color: #e2e2e2;
  width: 280px;
}
@media (min-width: 768px) {
  .range-scale {
    width: 220px;
  }
}
@media (min-width: 1440px) {
  .range-scale {
    width: 318px;
  }
}

.range-bar {
  position: absolute;
  height: 4px;
  width: 242px;
  background-color: #9070ec;
}

.range-toggle {
  position: absolute;
  width: 24px;
  height: 24px;
  padding: 0;
  background: transparent;
  border: 2px solid #9070EC;
  background-color: #f8f8f8;
  border-radius: 50%;
  cursor: pointer;
}

.range-toggle::after {
  content: " ";
  height: 16px;
  width: 16px;
  background-color: rgba(120, 89, 207, 0.25);
  top: 2px;
  left: 2px;
  position: absolute;
  border-radius: 50%;
  display: none;
}

.range-toggle:hover::after,
.range-toggle:focus::after {
  display: block;
}

.range-toggle:active::after {
  display: block;
  background-color: #7859CF;
}

.range-toggle:focus {
  border: 2px solid #AF4FFF;
  background-color: #EBEBEB;
  outline: none;
}

.toggle-min {
  top: 0;
  left: 0px;
  transform: translateY(-10px);
}

.toggle-max {
  top: 0;
  right: 0;
  transform: translateY(-10px);
}

