.control {
  display: flex;
  align-items: center;
}

.control-mark {
  position: relative;
  display: inline-block;
  width: 24px;
  height: 24px;
  border: 2px solid #bdbdbd;
  border-radius: 50%;
  margin-right: 10px;
}

.control-mark::after {
  content: "";
  width: 16px;
  height: 16px;
  background-color: #7859cf;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
}

.control-input:checked + .control-mark {
  border-color: #7859cf;
}

.control-input:checked + .control-mark::after {
  display: block;
}

.control-mark-checkbox {
  position: relative;
  display: inline-block;
  width: 42px;
  height: 24px;
  background-color: #bdbdbd;
  border-radius: 12px;
  margin-right: 10px;
}

.control-mark-checkbox::after {
  content: "";
  width: 20px;
  height: 20px;
  background-color: #ffffff;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 2px;
  transform: translateY(-50%);
  transition: left 0.2s ease;
}

.control-input:checked + .control-mark-checkbox {
  background-color: #7859cf;
}

.control-input:checked + .control-mark-checkbox::after {
  left: 20px;
}

