.range-scale {
  position: relative;
  height: 2px;
  margin-bottom: 25px;
  background-color: #e2e2e2;
  width: 280px;

  @media (min-width: $tablet-width) {
    width: 220px;
  }

  @media (min-width: $desktop-width) {
    width: 318px;
  }
}

.range-bar {
  position: absolute;
  height:4px;
  width: 242px;
  background-color: #9070ec;
}

.range-toggle {
  position: absolute;
  width: 24px;
  height: 24px;
  padding: 0;
  background: transparent;
  border: 2px solid #9070EC;
  background-color: #f8f8f8;
  border-radius: 50%;
  cursor: pointer;
}

.range-toggle::after {
  content: " ";
  height: 16px;
  width: 16px;
  background-color: rgba(120, 89, 207, 0.25);
  top: 2px;
  left: 2px;
  position: absolute;
  border-radius: 50%;
  display: none;
}

.range-toggle:hover::after,
.range-toggle:focus::after {
  display: block;
}

.range-toggle:active::after {
  display: block;
  background-color: #7859CF;
}

.range-toggle:focus {
  border: 2px solid #AF4FFF;
  background-color: #EBEBEB;
  outline: none;
}

.toggle-min {
  top: 0;
  left: 0px;
  transform: translateY(-10px);
}

.toggle-max {
  top: 0;
  right: 0;
  transform: translateY(-10px);
}
.catalog-filter-slider {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.catalog-filter-label {
  display: flex;
  align-items: flex-end;
  font-size: 15px;
  line-height: 18px;
  margin-bottom: 36px;
}

.catalog-filter-input {
  background-color: #ffffff;
  border: none;
  border: 2px solid #9070EC;
  border-radius: 4px;
  width: 100%;
  padding: 0 10px;
  appearance: textfield;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  width: 118px;
  height: 38px;
  color: #333333;

  @media (min-width: $tablet-width) {
    width: 92px;
  }

  @media (min-width: $desktop-width) {
    width: 134px;
  }
}

.catalog-filter-slider::after {
  content: '';
  width: 9px;
  height: 2px;
  background-color: #bdbdbd;
  position: absolute;
  top: 18px;
  left: 49%;
}

.catalog-filter-input::-webkit-inner-spin-button,
.catalog-filter-input::-webkit-outer-spin-button {
  appearance: none;
}

.catalog-filter-input::placeholder {
  color: #bdbdbd;
}

.catalog-filter-input:focus {
  outline: none;
  border-color: #593cac;
}
